"use client";
// ... (keep all other imports the same)

// CHANGE THIS IMPORT
import { auth } from '@/firebase/client'; // Instead of adminAuth
// Keep all other imports exactly the same

// ... (keep all other code exactly the same until onSubmit)

async function onSubmit(values: z.infer<typeof formSchema>) {
    try{
      if(type === 'sign-up'){
        const { name, email, password } = values;

        // CHANGE THIS LINE - use auth instead of adminAuth
        const userCredentials = await createUserWithEmailAndPassword(auth, email, password);

        const result = await signUp({
          uid: userCredentials.user.uid,
          name: name!,
          email,
          password,
        });

        if(!result?.success){
          toast.error(result?.message);
          return;
        }

        toast.success('Account created successfully. Please sign in.')
        router.push('/sign-in')

      } else {
        const { email, password } = values;

        // CHANGE THIS LINE - use auth instead of adminAuth
        const userCredential = await signInWithEmailAndPassword(auth, email, password);

        const idToken = await userCredential.user.getIdToken();
        if(!idToken){
          toast.error('Sign in failed');
          return;
        }
        
        await signIn({
          email,
          idToken
        });

        toast.success('Sign in successfully.')
        router.push('/')
      }
    }
    catch (error) {
      console.log(error);
      toast.error(`There was an error: ${error}`);
    }
};

// ... (keep all remaining code exactly the same)